.container-card-add{
    margin-bottom: 20px;
    margin-right: 20px;
    padding: 0;
    background-color: #E6E6E6;
    display: flex;
    flex-direction: column;
    align-content: center;
    border-radius: 5%;
}

.sub-container-add{
    flex: 1;
    margin: 10px;
    position: relative;
}

.layer-opacity-add{
    z-index: 0;
    position: absolute;
    width: 100%;
    height: 100%;
    opacity: 0.6;
    background-color: #000000;
    opacity: 12%;
    border-radius: 5%;
}

.info-card-add{
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 2;
    padding: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.ic-add{
    width: 40px;
    height: 40px;
}

.txt-add{
    text-align: center;
    color: #fff;
}